      <div class="story section">
        <div>
          <h2>Chinese Vocabulary Discovery Tool</h2>
          <p class="left-align">
            When I decided to teach myself Chinese in the summer of 2018, I
            studied the most common hanzi characters in order of frequency. As I
            was exposed to more and more hanzi, I found that I could pronounce
            many more words made up of those characters, even if I didn't quite
            know what they meant. To facilitate my vocabulary learning, I
            created the Chinese Vocabulary Discovery Tool. It's a Python program
            that, given a list of characters a learner knows,
            <span
              class="orange"
            >
              searches a dictionary or the offical HSK word list to find what
              words the learner can read
            </span>
            . This program helped me tremendously in finding new words that I
            could learn, and every character I studied thereafter increased my
            vocabulary pool exponentially.
          </p>
        </div>
        <div>
