        </div>
        <div>
          <h2>Haggle</h2>
          <p class="right-align">
            <a href="https://en.wikipedia.org/wiki/Haggle_(game)">Haggle</a> is
            a game traditionally played with large parties or even entire
            conventions wherein a gamemaster makes up scoring rules for playing
            cards. Participants are then given a random starting set of cards
            and some (but not all) of the rules. Each participant has different
            cards and different known rules. The game revolves around trading
            cards for other cards or scoring information to obtain the highest
            point total at the end, when all the scoring rules are revealed.
            This sounded like a really fun game to play, but the gamemaster
            can't participate since they are privy to all the rules from the
            start. Thus, I developed a
            <span
              class="orange"
            >
              token-based generative grammar program to
              automatically create and resolve scoring rules
            </span> so that even the game
            organizer can join in the fun!
